/* label */
label {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: nowrap;
  margin: 12px 0;
  cursor: pointer;
  position: relative;
}


/* input */
.input-checkbox {
  display: none;
}


/* .design */
.design {
  width: 16px;
  height: 16px;
  border: 2px solid #B1B1B1;
  border-radius: 100%;
  position: absolute;
  right: 15px;
  box-sizing: content-box;
}

.design::before,
.design::after {
  content: "";
  display: block;
  width: inherit;
  height: inherit;
  border-radius: inherit;
  position: absolute;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: center center;
          transform-origin: center center;
}

.design:before {
  background: var(--other);
  opacity: 0;
  transition: .3s;
}

.design::after {
  background: var(--primary);
  opacity: .4;
  transition: .6s;
}

/* checked state */
input:checked+.design::before {
  opacity: 1;
  -webkit-transform: scale(.6);
          transform: scale(.6);
  background: var(--primary);
}

input:checked+.design {
  border: 2px solid var(--primary);
}


input:checked+.design::after {
  opacity: .1;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

